  font-style: normal;
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff"), url("/fonts/pt-mono-regular.woff"); }

img {
  width: 100%;
  height: auto; }

.page {
  min-width: 320px;
  display: grid;
  grid-template-rows: auto 1fr auto;
  box-sizing: border-box;
  font-family: "Roboto Condensed", "Helvetica", sans-serif; }

a {
  text-decoration: none;
  color: inherit; }

li {
  list-style: none; }

ul {
  padding-left: 0; }

.header {
  padding-top: 33px;
  padding-left: 25px;
  padding-right: 25px;
  background: transparent;
  margin-bottom: -85px;
  position: relative;
  z-index: 2;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .header {
      padding-top: 20px; } }
  @media (max-width: 767px) {
    .header {
      padding-top: 17px;
      padding-left: 17px;
      padding-right: 17px; } }

.header__container {
  max-width: 1411px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.header__contacts {
  display: flex;
  flex-direction: row; }

.header__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 402px;
  margin-right: 38px;
  font-size: 16px;
  font-family: "PT Mono"; }
  @media (max-width: 1023px) {
    .header__list {
      width: 82px;
      margin-right: 0; } }
  @media (max-width: 767px) {
    .header__list {
      width: 72px; } }

.header__item {
  position: relative; }

.header__email {
  display: block;
  position: absolute;
  left: -39px;
  top: 2px; }
  @media (max-width: 1023px) {
    .header__email {
      position: unset;
      width: 26px;
      height: 19px;
      transform: translateY(3px); } }

.header__phone {
  display: block;
  position: absolute;
  left: -34px; }
  @media (max-width: 1023px) {
    .header__phone {
      position: unset;
      width: 23px;
      height: 23px; } }

@media (max-width: 1023px) {
  .header__span {
    display: none; } }

.header__button {
  width: 180px;
  border: 1px solid #ffffff; }
  @media (max-width: 1023px) {
    .header__button {
      display: none; } }

.button {
  outline: none;
  background-color: transparent;
  height: 45px;
  line-height: 17px;
  font-size: 14px;
  text-transform: uppercase;
  border-radius: 5px;
  font-family: "Roboto Condensed";
  font-weight: 700;
  color: inherit;
  letter-spacing: 1px;
  cursor: pointer; }

.promo {
  background-color: #1B2D37;
  padding-top: 258px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 278px;
  color: #ffffff;
  background-image: url("../../img/noise.png"), url("../../img/triangles.svg"); }
  @media (max-width: 1023px) {
    .promo {
      padding-top: 113px;
      padding-bottom: 163px;
      background-position: center -187px; } }
  @media (max-width: 767px) {
    .promo {
      padding-left: 15px;
      padding-right: 15px;
      padding-bottom: 70px; } }

.promo__container {
  max-width: 1566px;
  margin: 0 auto;
  padding-left: 72px;
  box-sizing: border-box;
  position: relative; }
  @media (max-width: 1023px) {
    .promo__container {
      padding-left: 68px; } }
  @media (max-width: 767px) {
    .promo__container {
      padding-left: 0; } }

.promo__left-side {
  padding-top: 58px;
  position: relative;
  z-index: 2; }
  @media (max-width: 1023px) {
    .promo__left-side {
      padding-top: 75px; } }
  @media (max-width: 767px) {
    .promo__left-side {
      padding-top: 194px; } }

.promo__right-side {
  position: absolute;
  right: 0;
  top: 0;
  max-width: 705px;
  width: 47.2%; }
  @media (max-width: 1023px) {
    .promo__right-side {
      right: -20px;
      width: 233px;
      overflow: hidden; } }
  @media (max-width: 767px) {
    .promo__right-side {
      left: 4px;
      width: 301px; } }

.promo__title {
  margin: 0;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 6px;
  text-transform: uppercase; }
  @media (max-width: 1023px) {
    .promo__title {
      font-size: 60px;
      line-height: 70px;
      letter-spacing: 4px;
      width: 71.21%; } }
  @media (max-width: 767px) {
