      letter-spacing: 2.8px;
      line-height: 47px; } }

.promo__text {
  margin-top: 31px;
  margin-bottom: 37px;
  box-sizing: border-box;
  padding-left: 98px;
  position: relative;
  font-family: "PT Mono";
  font-size: 22px;
  line-height: 34px;
  max-width: 718px; }
  .promo__text::before {
    content: "";
    display: block;
    width: 66px;
    height: 2px;
    box-sizing: border-box;
    background-color: #ffffff;
    position: absolute;
    left: 4px;
    top: 18px; }
    @media (max-width: 1023px) {
