  @media (max-width: 1023px) {
    .promo__button {
      width: 379px;
      margin-left: 51px; } }
  @media (max-width: 767px) {
    .promo__button {
      width: 287px;
      margin-left: 3px; } }

@media (max-width: 767px) {
  .promo__span {
    display: none; } }

.promo__span--mobile {
  display: none; }
  @media (max-width: 767px) {
    .promo__span--mobile {
      display: inline; } }

.promo__scroll {
  position: absolute;
  transform: rotate(-90deg);
  opacity: 50%;
  bottom: -140px;
  left: 50px;
  font-size: 14px;
  font-weight: 400; }
  @media (max-width: 1023px) {
    .promo__scroll {
      display: none; } }
